.cart{padding-top:173px;background:linear-gradient(rgba(255, 206, 103, 0.22) -42.47%, rgba(253, 237, 202, 0) 100%);min-height:100vh}.cart__title{font-weight:700;font-size:28px;line-height:120%;color:#424242;margin-bottom:20px}.cart__inner{position:relative}.cart__top{display:flex;justify-content:space-between;align-items:center;margin-bottom:20px;padding-top:30px}.cart__list{max-width:1250px;margin-bottom:70px}.cart__name{font-weight:700;font-size:36px;line-height:112%;color:#f17228;margin-bottom:37px;padding-top:30px}.cart__empty{font-weight:400;font-size:20px;line-height:112%;color:#212121}.cart__result{font-weight:700;font-size:22px;line-height:120%;color:#424242;margin-bottom:20px;padding-right:20px}.cart__result_color{color:#f17228}.cart__message{font-weight:700;font-size:22px;line-height:120%;color:#424242;margin-bottom:20px}.cart__image{max-width:50%;border-radius:15px;margin-bottom:20px;filter:blur(1px)}.cart__empty{text-align:center}.cart__clearIcon{color:#9e9e9e;margin-right:8px;transition:all .3s ease}.cart__clearBtn{display:block;font-weight:400;font-size:22px;line-height:120%;color:#9e9e9e;background-color:rgba(0,0,0,0);margin-left:auto;transition:all .3s ease}.cart__clearBtn:hover{color:#424242;transition:transform .15s ease,color .3s ease;transform:translateY(1px)}.cart__btnOrder{width:fit-content}.cart__orderInfo{display:flex;flex-wrap:wrap;justify-content:space-between;align-items:center;padding-bottom:70px}.cart__clearBtn:hover .cart__clearIcon{color:#424242;transition:all .3s ease}.cart__restaurantName{font-weight:700;font-size:28px;line-height:120%;color:#f17228}.cart__links{font-weight:400;font-size:22px;line-height:120%}.cart__linkItem{color:#f17228;font-weight:600;transition:all .3s ease}.cart__linkItem:hover{color:#f17228;transition:all .3s ease}.popup__name{color:#b14a09;font-weight:700}.cart__operatStatus{font-size:16px}.cart__distanceItem{font-size:16px}.cart__address{padding:24px;background-color:#fff;border-radius:0 0 8px 8px}.cart__status{display:flex;flex-wrap:wrap;align-items:center;gap:25px;margin-right:15px}.cart__deliveryСhoice{margin-bottom:20px;box-shadow:0 0 25px 0 rgba(0,0,0,.15);max-width:856px;border-radius:8px}@media(max-width: 1350px){.cart__list{max-width:900px}.cart__result{max-width:calc(100% - 407px)}}@media(max-width: 800px){.cart__result{max-width:100%}}@media(max-width: 500px){.cart__orderBtn{width:100%;padding:21px}}