@import "../../../styles/variables";

.delivery {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;

  padding: 24px;

  border-bottom: 2px solid $main-color-white;
  background-color: $white-color;
  border-radius: 8px 8px 0 0;
}

.btnActive {
  background-color: rgba($base-color, 0.1);
  color: $base-color;
}

.btnActive span>svg>path {
  fill: $base-color;
}