.cart{max-width:1250px;margin-bottom:70px}.cart__top{display:flex;justify-content:space-between;align-items:center;margin-bottom:20px;padding-top:30px}.cart__status{display:flex;flex-wrap:wrap;align-items:center;gap:25px;margin-right:15px}.cart__restaurantName{font-weight:700;font-size:28px;line-height:120%;color:#f17228}.cart__operatStatus{font-size:16px}.cart__distanceItem{font-size:16px}.cart__clearIcon{color:#9e9e9e;margin-right:8px;transition:all .3s ease}.cart__clearBtn:hover{color:#424242;transition:all .3s ease}.cart__clearBtn:hover .cart__clearIcon{color:#424242;transition:all .3s ease}.cart__clearBtn{display:block;font-weight:400;font-size:22px;line-height:120%;color:#9e9e9e;background-color:rgba(0,0,0,0);margin-left:auto;transition:all .3s ease}.cart__deliveryСhoice{margin-bottom:20px;box-shadow:0 0 25px 0 rgba(0,0,0,.15);border-radius:8px}.cart__orderInfo{display:flex;flex-wrap:wrap;justify-content:space-between;align-items:center;padding-bottom:70px}@media(max-width: 1350px){.cart{max-width:900px}}@media(max-width: 500px){.cart__orderBtn{width:100%;padding:21px}}